<template>
  <v-dialog v-model="dialog" @afterLeave="reset()" width="auto">
    <template v-slot:activator="{ props: activatorProps }">
      <v-btn color="success" append-icon="mdi-plus" v-bind="activatorProps"
        >Novo Membro</v-btn
      >
    </template>
    <template v-slot:default="{}">
      <v-card class="mx-auto" min-width="400">
        <v-card-title class="bg-success">{{
          steps[step].title
        }}</v-card-title>
        <v-window v-model="step">
          <v-window-item :value="0">
            <v-card-text>
              <v-form v-model="steps[0].formValid">
                <v-autocomplete
                  class="mt-4"
                  v-model="member"
                  :items="agents"
                  item-title="username"
                  item-value="id"
                  prepend-inner-icon="mdi-account"
                  return-object
                  placeholder="Insira o nome do utilizador"
                  no-data-text="Nenhum utilizador encontrado"
                  color="secondary"
                  density="compact"
                  variant="outlined"
                  :rules="[(v) => !!v || 'Campo obrigatório']"
                  :loading="isLoadingAgents"
                  @update:search="listAgents($event)"
                >
                  <template v-slot:item="{ props, item }">
                    <v-list-item v-bind="props">
                      <template v-slot:subtitle>
                        <div class="d-flex justify-space-between align-center">
                          <span>{{ item.raw.name }}</span>
                          <v-chip
                            size="small"
                            :color="roleColor(item.raw.type)"
                            variant="flat"
                            >{{ item.raw.type }}</v-chip
                          >
                        </div>
                      </template>
                    </v-list-item>
                  </template>
                </v-autocomplete>
              </v-form>
            </v-card-text>
          </v-window-item>
          <v-window-item :value="1">
            <v-card-text>
              <v-form v-model="steps[1].formValid">
                <v-locale-provider locale="pt">
                  <div class="mt-4">
                    <v-date-input
                      v-model="startDate"
                      label="Data de Início"
                      prepend-icon=""
                      prepend-inner-icon="mdi-calendar-start"
                      color="secondary"
                      density="compact"
                      variant="outlined"
                      :rules="[(v) => !!v || 'Campo obrigatório']"
                    ></v-date-input>
                  </div>
                </v-locale-provider>
              </v-form>
            </v-card-text>
          </v-window-item>
        </v-window>
        <v-card-actions>
          <v-btn
            v-if="step > 0"
            prepend-icon="mdi-chevron-left"
            variant="tonal"
            @click="step--"
          >
            Anterior
          </v-btn>
          <v-spacer></v-spacer>
          <v-btn
            v-if="step < lastStep"
            :disabled="!steps[step].formValid"
            append-icon="mdi-chevron-right"
            color="secondary"
            variant="tonal"
            @click="step++"
          >
            Próximo
          </v-btn>
          <v-btn
            v-else
            :disabled="!steps[step].formValid"
            color="success"
            variant="flat"
            :loading="isLoadingPost"
            @click="postMember()"
          >
            Adicionar
          </v-btn>
        </v-card-actions>
      </v-card>
    </template>
  </v-dialog>
</template>

<script>
import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";
export default {
  name: "AddMember",
  props: {
    organization_id: Number,
    existingMembers: Array,
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();

    return { accountStore, generalStore };
  },
  data() {
    return {
      dialog: false,
      step: 0,
      steps: [
        { title: "Utilizador", icon: "mdi-account", formValid: false },
        { title: "Data de Início", icon: "mdi-calendar", formValid: false },
      ],
      //utilizador
      agents: [],
      isLoadingAgents: false,
      member: null,
      startDate: new Date(),
      isLoadingPost: false,

      //data de inicio
      startDate: new Date(),
    };
  },
  watch: {
    roleSelected(value) {
      if (value) {
        this.member = null;
      }
    },
    member(value) {
      if (value) {
        console.log(value);
      }
    },
  },
  computed: {
    lastStep() {
      return this.steps.length - 1;
    },
    existingAgentIds() {
      return this.existingMembers.map((existing) => existing.id);
    },
  },
  methods: {
    reset() {
      this.step = 0;
      this.member = null;
      this.agents = [];
      this.startDate = new Date();
      this.isLoadingPost = false;
    },
    roleColor(role) {
      switch (role) {
        case "Administrador":
          return "error";
        case "Organização":
          return "warning";
        case "Produtor":
          return "success";
        case "Agente de Campo":
          return "info";
        default:
          return "primary";
      }
    },
    async listAgents(filter) {
      if (!filter) return;
      this.isLoadingAgents = true;
      //   await new Promise((resolve) => setTimeout(resolve, 2000));

      await axios
        .request({
          url: "api/freeagents",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          params: {
            username: filter,
          },
        })
        .then((response) => {
          this.agents = response.data.agents;
          this.isLoadingAgents = false;
        });
    },
    async postMember() {
      console.log("postMember");
      this.isLoadingPost = true;
      let payload = {
        agent_id: this.member.id,
        organization_id: this.organization_id,
        startDate: this.startDate,
      };
      console.log(payload);
      await axios
        .request({
          url: "api/organizationmembers",
          method: "POST",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: payload,
        })
        .then((response) => {
          this.isLoadingPost = false;
          this.dialog = false;
          this.generalStore.successSnackbar("Membro adicionado com sucesso!");
          this.$emit("refresh");
        })
        .catch((error) => {
          console.error(error);
          this.isLoadingPost = false;
          this.generalStore.errorSnackbar("Erro ao adicionar membro.");
        });
    },
  },
};
</script>

