<template>
  <v-container fluid class="fill-width">
    <v-row>
      <v-col cols="12">
        <v-card elevation="8">
          <v-card-title>Filtro</v-card-title>
          <v-card-text>
            <v-row>
              <v-col v-if="accountStore.hasRole(['Administrador'])">
                <v-autocomplete
                  clearable
                  density="comfortable"
                  variant="underlined"
                  color="primary"
                  label="Organização"
                  :disabled="isLoadingTable || isLoadingOrgs"
                  :items="organizations"
                  item-title="organization_name"
                  item-value="organization_id"
                  v-model="filter.organization_id"
                ></v-autocomplete>
              </v-col>
              <v-col v-if="accountStore.hasRole(['Administrador'])">
                <v-select
                  clearable
                  density="comfortable"
                  variant="underlined"
                  color="primary"
                  label="Tipo de Conta"
                  :disabled="isLoadingTable || isLoadingAgentTypes"
                  :items="agentTypes"
                  item-title="agenttype_name"
                  item-value="agenttype_id"
                  v-model="filter.type"
                ></v-select>
              </v-col>
              <v-col v-if="accountStore.hasRole(['Organização'])">
                <v-select
                  clearable
                  density="comfortable"
                  variant="underlined"
                  color="primary"
                  label="Tipo de Membro"
                  :disabled="isLoadingTable || isLoadingMemberTypes"
                  :items="memberTypes"
                  item-title="membertype_name"
                  item-value="membertype_id"
                  v-model="filter.type"
                ></v-select>
              </v-col>
              <v-col>
                <v-text-field
                  clearable
                  density="comfortable"
                  variant="underlined"
                  color="primary"
                  label="Conta"
                  :disabled="isLoadingTable"
                  v-model="filter.username"
                ></v-text-field>
              </v-col>
              <v-col>
                <v-text-field
                  clearable
                  density="comfortable"
                  variant="underlined"
                  color="primary"
                  label="Nome"
                  :disabled="isLoadingTable"
                  v-model="filter.name"
                ></v-text-field>
              </v-col>
              <v-col>
                <v-text-field
                  clearable
                  density="comfortable"
                  variant="underlined"
                  color="primary"
                  label="Telefone"
                  :disabled="isLoadingTable"
                  v-model="filter.phone"
                ></v-text-field>
              </v-col>
              <v-col>
                <v-text-field
                  clearable
                  density="comfortable"
                  variant="underlined"
                  color="primary"
                  label="Email"
                  :disabled="isLoadingTable"
                  v-model="filter.email"
                ></v-text-field>
              </v-col>
              <v-col>
                <v-btn
                  color="secondary"
                  icon
                  variant="text"
                  @click="refreshAgents()"
                  :disabled="isLoadingTable"
                >
                  <v-icon size="x-large">mdi-magnify</v-icon>
                </v-btn>
              </v-col>
              <v-col v-if="accountStore.hasRole(['Organização'])">
                <AddMember
                  :organization_id="accountStore.account.organization"
                  :existingMembers="agents"
                  @refresh="refreshAgents()"
                ></AddMember>
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <v-row>
      <v-col>
        <AgentsTable
          :agents="agents"
          :hasMore="hasMore"
          :isLoadingTable="isLoadingTable"
          @loadMore="getAgents()"
          @refresh="refreshAgents()"
        ></AgentsTable>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import AgentsTable from "@/components/Agents/AgentsTable.vue";
import AddMember from "@/components/Agents/AddMember.vue";

import { useAccountStore } from "@/stores/accountStore";
import axios from "axios";

export default {
  name: "AgentsView",
  components: {
    AgentsTable,
    AddMember,
  },
  setup() {
    const accountStore = useAccountStore();
    return { accountStore };
  },
  data() {
    return {
      isLoadingTable: false,
      agents: [],
      filter: {
        organization_id: null,
        type: null,
        username: null,
        name: null,
        phone: null,
        email: null,
      },
      limit: 12,
      offset: 0,
      hasMore: false,
      // organizations
      isLoadingOrgs: false,
      organizations: [],
      //agents types
      isLoadingAgentTypes: false,
      agentTypes: [],

      //member types
      isLoadingMemberTypes: false,
      memberTypes: [],
    };
  },
  watch: {},
  computed: {},
  methods: {
    async getAgents() {
      this.isLoadingTable = true;
      await axios
        .request({
          url: "api/agents",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          params: {
            limit: this.limit,
            offset: this.offset,
            // filter search
            organization_id: this.filter.organization_id,
            type: this.filter.type,
            username: this.filter.username,
            name: this.filter.name,
            phone: this.filter.phone,
            email: this.filter.email,
          },
        })
        .then((response) => {
          this.agents.push(...response.data.agents);
          this.offset += response.data.agents.length;
          this.hasMore = response.data.hasMore;
          this.isLoadingTable = false;
        })
        .catch((error) => {
          console.log(error);
          this.isLoadingTable = false;
        });
    },
    refreshAgents() {
      this.offset = 0;
      this.agents = [];
      this.getAgents();
    },
    async getOrganizations() {
      this.isLoadingOrgs = true;
      await axios
        .request({
          url: "api/organizations",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.organizations = response.data.organizations;
          this.isLoadingOrgs = false;
        })
        .catch((error) => {
          console.log(error);
          this.isLoadingOrgs = false;
        });
    },

    async getAgentTypes() {
      this.isLoadingAgentTypes = true;
      await axios
        .request({
          url: "api/agenttypes/roles",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.agentTypes = response.data;
          this.isLoadingAgentTypes = false;
        })
        .catch((error) => {
          console.log(error);
          this.isLoadingAgentTypes = false;
        });
    },

    async getMemberTypes() {
      this.isLoadingMemberTypes = true;
      await axios
        .request({
          url: "api/membertypes",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.memberTypes = response.data;
          this.isLoadingMemberTypes = false;
        })
        .catch((error) => {
          console.log(error);
          this.isLoadingMemberTypes = false;
        });
    },
  },
  mounted() {
    this.getAgents();
    if (this.accountStore.hasRole(["Administrador"])) {
      this.getOrganizations();
      this.getAgentTypes();
    } else if (this.accountStore.hasRole(["Organização"])) {
      this.getMemberTypes();
    }
  },
};
</script>

<style></style>
