<template>
  <v-sheet class="pa-2" elevation="8" rounded="lg" width="100%">
    <v-data-table-virtual
      fixed-header
      no-data-text="Nenhum utilizador encontrado"
      loading-text="A carregar utilizadores..."
      :headers="headers"
      :items="tableItems"
      :loading="isLoadingAgents"
      :height="tableHeight"
    >
      <template v-slot:item="row">
        <tr>
          <td>
            <v-btn
              color="info"
              variant="text"
              icon="mdi-information-outline"
              @click="openDetailDialog(row.item, false)"
            ></v-btn>
          </td>
          <td v-if="accountStore.hasRole(['Administrador'])">
            <v-chip v-if="row.item.organization" color="warning">
              {{ row.item.organization.organization_name }}</v-chip
            >
          </td>
          <td>
            <v-chip label :color="roleColor(row.item.type)" variant="flat"
              >{{ row.item.type }}
            </v-chip>
          </td>
          <td>{{ row.item.username }}</td>
          <td>{{ row.item.name }}</td>
          <!-- <td>{{ row.item.phone }}</td>
          <td>{{ row.item.email }}</td> -->
          <td class="text-center">
            <v-chip :color="row.item.accepted ? 'success' : 'error'">{{
              row.item.accepted ? "Aceite" : "Não aceite"
            }}</v-chip>
            <v-chip
              v-if="accountStore.hasRole(['Administrador'])"
              :color="row.item.blocked ? 'error' : 'success'"
              >{{ row.item.blocked ? "Bloqueado" : "Não bloqueado" }}</v-chip
            >
            <v-chip
              v-if="accountStore.hasRole(['Administrador'])"
              :color="row.item.active ? 'success' : 'error'"
              >{{ row.item.active ? "Ativo" : "Inativo" }}</v-chip
            >
          </td>
          <td
            v-if="accountStore.hasRole(['Administrador'])"
            class="d-flex justify-space-evenly nowrap"
          >
            <v-btn
              color="warning"
              variant="text"
              icon
              @click="openDetailDialog(row.item, true)"
            >
              <v-icon>mdi-pencil</v-icon>
              <v-tooltip activator="parent" location="bottom">
                Editar
              </v-tooltip>
            </v-btn>
            <v-btn
              color="error"
              variant="text"
              icon
              @click="openDeleteDialog(row.item)"
            >
              <v-icon>mdi-delete-outline</v-icon>
              <v-tooltip activator="parent" location="bottom">
                Remover conta
              </v-tooltip>
            </v-btn>
          </td>
          <td
            v-if="accountStore.hasRole(['Organização'])"
            class="d-flex justify-space-evenly nowrap"
          >
            <v-btn
              v-if="row.item.accepted"
              color="error"
              variant="text"
              icon
              :loading="row.item.loading"
              @click="changeStatus(row.item, false)"
            >
              <v-icon>mdi-account-remove</v-icon>
              <v-tooltip activator="parent" location="bottom">
                Rejeitar membro
              </v-tooltip>
            </v-btn>
            <v-btn
              v-else
              color="success"
              variant="text"
              icon
              :loading="row.item.loading"
              @click="changeStatus(row.item, true)"
            >
              <v-icon>mdi-account-check</v-icon>
              <v-tooltip activator="parent" location="bottom">
                Aceitar membro
              </v-tooltip>
            </v-btn>

            <v-btn
              color="error"
              variant="text"
              icon
              @click="openDeleteDialog(row.item)"
            >
              <v-icon>mdi-logout</v-icon>
              <v-tooltip activator="parent" location="bottom">
                Remover membro
              </v-tooltip>
            </v-btn>
          </td>
        </tr>
      </template>
      <template v-if="hasMore && !isLoadingAgents" v-slot:[`body.append`]>
        <tr
          v-intersect.quiet="{
            handler: loadMore,
            options: {
              threshold: 0.5,
            },
          }"
        >
          <td :colspan="headers.length" class="text-center">
            <v-skeleton-loader type="table-row"></v-skeleton-loader>
          </td>
        </tr>
      </template>
    </v-data-table-virtual>
    <v-dialog
      v-if="accountStore.hasRole(['Organização'])"
      v-model="deleteDialog"
      @afterLeave="reset()"
      persistent
      width="auto"
    >
      <v-card>
        <v-card-title class="bg-error d-flex align-center">
          <v-icon size="small" class="mr-2"> mdi-logout </v-icon>
          <span>Remover membro</span></v-card-title
        >
        <v-card-text>
          <span
            >Tem a certeza que pretende remover o membro
            <b>{{ agent?.username }}</b> da organização?</span
          >
          <v-form v-model="deleteFormValid">
            <div class="mt-4">
              <v-local-provider locale="pt">
                <v-date-input
                  v-model="endDate"
                  label=" Ultima data de atuação"
                  prepend-icon=""
                  prepend-inner-icon="mdi-calendar-end"
                  color="secondary"
                  density="compact"
                  variant="outlined"
                  :rules="[(v) => !!v || 'Campo obrigatório']"
                ></v-date-input>
              </v-local-provider>
            </div>
          </v-form>
        </v-card-text>
        <v-card-actions class="d-flex justify-space-around">
          <v-btn
            color="error"
            variant="tonal"
            icon="mdi-close"
            @click="deleteDialog = false"
          ></v-btn>
          <v-btn
            color="secondary"
            variant="tonal"
            icon="mdi-check"
            :loading="isLoading"
            @click="deleteMember()"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog
      v-else-if="accountStore.hasRole(['Administrador'])"
      v-model="deleteDialog"
      @afterLeave="reset()"
      persistent
      width="auto"
    >
      <v-card>
        <v-card-title class="bg-error d-flex align-center">
          <v-icon size="small" class="mr-2">mdi-delete-outline</v-icon>
          <span>Remover Utilizador</span></v-card-title
        >
        <v-card-text>
          <span
            >Tem a certeza que pretende remover o utilizador
            <b>{{ agent?.username }}</b
            >?</span
          >
        </v-card-text>
        <v-card-actions class="d-flex justify-space-around">
          <v-btn
            color="error"
            variant="tonal"
            icon="mdi-close"
            @click="deleteDialog = false"
          ></v-btn>
          <v-btn
            color="secondary"
            variant="tonal"
            icon="mdi-check"
            :loading="isLoading"
            @click="deleteAgent()"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <AgentDetail
      :agent="agent"
      :editMode="editMode"
      :dialog="detailDialog"
      @update:dialog="(value) => (detailDialog = value)"
      @refresh="() => $emit('refresh')"
    ></AgentDetail>
  </v-sheet>
</template>

<script>
import AgentDetail from "@/components/Agents/AgentDetail.vue";

import { useGeneralStore } from "@/stores/generalStore";
import { useAccountStore } from "@/stores/accountStore";

import axios from "axios";
export default {
  name: "AgentsTable",
  components: {
    AgentDetail,
  },
  props: {
    agents: Array,
    isLoadingAgents: Boolean,
    hasMore: Boolean,
  },
  setup() {
    const generalStore = useGeneralStore();
    const accountStore = useAccountStore();
    return { generalStore, accountStore };
  },
  data() {
    return {
      isLoading: false,
      detailDialog: false,
      editFormValid: false,
      editMode: false,
      agent: null,
      //delete
      deleteDialog: false,
      deleteFormValid: false,
      endDate: new Date(),
      isLoadingDelete: false,
      //status
      isLoadingStatus: false,
    };
  },
  computed: {
    tableHeight() {
      return window.innerHeight - 270;
    },
    headers() {
      switch (this.accountStore.Role()) {
        case "Administrador":
          return [
            {
              title: "",
              align: "center",
            },
            {
              title: "Organização",
              key: "organization",
            },
            {
              title: "Tipo",
              key: "type",
            },
            {
              title: "Conta",
              key: "username",
            },

            {
              title: "Nome",
              key: "name",
            },
            // {
            //   title: "Telefone",
            //   key: "phone",
            // },
            // {
            //   title: "Email",
            //   key: "email",
            // },
            {
              title: "Estado",
              align: "center",
            },
            {
              title: "",
            },
            // {
            //   title: "",
            //   key: "accepted",
            // },
            // {
            //   title: "",
            //   key: "blocked",
            // },
            // {
            //   title: "",
            //   key: "active",
            // },
          ];
        case "Organização":
          return [
            {
              title: "",
              align: "center",
            },
            {
              title: "Tipo",
              key: "type",
            },
            {
              title: "Conta",
              key: "username",
            },
            {
              title: "Nome",
              key: "name",
            },
            // {
            //   title: "Telefone",
            //   key: "phone",
            // },
            // {
            //   title: "Email",
            //   key: "email",
            // },
            {
              title: "Estado",
              align: "center",
            },
            {
              title: "",
            },
          ];
        default:
          return [];
      }
    },
    me() {
      return this.accountStore.account;
    },
    tableItems() {
      return this.agents.filter((agent) => {
        return agent.id != this.me?.id;
      });
    },
  },
  methods: {
    async changeStatus(member, status) {
      member.loading = true;
      console.log(member);
      // await new Promise((resolve) => setTimeout(resolve, 2000));
      await axios
        .request({
          url: "api/organizationmembers/" + member.member_id + "/status",
          method: "patch",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: {
            status: status,
          },
        })
        .then((response) => {
          member.loading = false;
          this.generalStore.successSnackbar(
            "Estado do membro alterado com sucesso"
          );
          this.$emit("refresh");
        })
        .catch((error) => {
          member.loading = false;
          console.log(error);
          this.generalStore.errorSnackbar("Erro ao alterar estado do membro");
        });
    },
    deleteMember() {
      this.isLoading = true;
      console.log(this.agent);
      axios
        .request({
          url: "api/organizationmembers/" + this.agent.member_id,
          method: "delete",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: {
            endDate: this.endDate,
          },
        })
        .then((response) => {
          this.generalStore.successSnackbar("Membro removido com sucesso");
          this.$emit("refresh");
          this.isLoading = false;
          this.deleteDialog = false;
        })
        .catch((error) => {
          console.log(error);
          this.isLoading = false;
          this.generalStore.errorSnackbar("Erro ao remover membro");
        });
    },
    deleteAgent() {
      this.isLoading = true;
      console.log(this.agent);
      axios
        .request({
          url: "api/agents/" + this.agent.id,
          method: "delete",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.generalStore.successSnackbar("Conta removida com sucesso");
          this.$emit("refresh");
          this.isLoading = false;
          this.deleteDialog = false;
        })
        .catch((error) => {
          console.log(error);
          this.isLoading = false;
          this.generalStore.errorSnackbar(error.response.data.error, 3000);
        });
    },
    showInfo(agentId) {
      this.$emit("showInfo", agentId);
    },
    loadMore(isIntersecting) {
      if (isIntersecting) {
        this.$emit("loadMore");
      }
    },
    roleColor(role) {
      switch (role) {
        case "Administrador":
          return "error";
        case "Organização":
          return "warning";
        case "Produtor":
          return "success";
        case "Agente de Campo":
          return "info";
        default:
          return "primary";
      }
    },
    openDetailDialog(agent, editMode) {
      this.agent = agent;
      this.editMode = editMode;
      this.detailDialog = true;
    },
    openDeleteDialog(agent) {
      this.agent = agent;
      this.deleteDialog = true;
    },
    reset() {
      this.agent = null;
      this.isLoading = false;
    },
  },
};
</script>

<style></style>
